var that;
export default {
	data() {
		return {
			list: [],
			id:'',
			type:'',
			page:1,
			nolist: false,
			nomore: false
		};
	},
	methods: {
		tpinfo(e) {
			uni.navigateTo({
				url: '/pages/academy/videoInfo/videoInfo?id='+e.currentTarget.dataset.id
			});
		},
		getlist(){
			that.$request.request({
				url:that.api.college.list,
				data:{
					class_id:that.id,
					type:that.type,
					page:that.page
				}
			}).then(res=>{
				console.log('列表:', res);
				if (res.length > 0) {
					var list = res;
					that.list = that.list.concat(list);
					that.page = that.page + 1;
				} else {
					if (that.page != 1) {
						if (!that.nomore) {
							that.$alert('没有更多了!');
							that.nomore = true;
						}
					} else {
						that.nolist = true;
					}
				}
				that.$forceUpdate();
			})
		}
	},
	onLoad(option){
		console.log(option);
		that=this
		that.id=option.id?option.id:''
		that.type=option.type?option.type:''
		that.getlist();
	},
	onPullDownRefresh() {
		
	},
	onReachBottom() {
		that.getlist();
	}
};
